import {useState, useEffect} from 'react';
import classes from "./Slot.module.css";
import {getalltopperslots} from '../../../CommonApps/AllAPICalls';
import OneSlot from './OneSlot';



const Slots=(props)=> {


  const [topperSlots, getTopperSlots] = useState(null);

   const [pageNo, setPageNo] = useState(1);

   useEffect(()=>{
   let topperId=props.topperId;
   getalltopperslots({topperId, getTopperSlots, pageNo});

   },[pageNo,props.rerender]);


     const nextPageHandler=()=>{
         if(topperSlots !==null && topperSlots.next !==null ){
             setPageNo(pageNo=>pageNo+1);
         }
     }


     const prevPageHandler=()=>{
          if(topperSlots !==null && topperSlots.previous !==null ){
            setPageNo(pageNo=>pageNo-1);
          }
     }






   console.log("topperSlots: ", topperSlots);


  return <div className={classes.slotsContainer}>

          <h3> Available meeting  slots</h3>


             {topperSlots !==null && topperSlots.results.map((oneSlot, index)=>{

               return <OneSlot key={index}
		               oneSlot={oneSlot}
                                      />

                })

            }


	 </div>;
}
export default Slots;
